'strict mode'
module.exports = (() => {
  let currentGameState

  function ttt ({ gameBoard, lastPlayed, answerSymbol }) {
    currentGameState = {
      gameBoard,
      lastPlayed,
      answerSymbol,
      playerSymbol: gameBoard[lastPlayed] || 'firstMove',
      boardDimens: Math.sqrt(gameBoard.length)
    }
    return {
      hasWon: hasWon(currentGameState),
      isDraw: isDraw(currentGameState),
      answerMove: calcAnswerMove(currentGameState, 0).index
    }
  }

  function hasWon (gameState) {
    return hasWonWith(gameState).length > 0
  }

  function isDraw (gameState) {
    let movesDone = gameState.gameBoard.filter(x => x).length
    return !hasWon(gameState) && movesDone === gameState.gameBoard.length
  }

  function hasWonWith (gameState) {
    return getSetsWithNPlayerTiles(gameState, gameState.boardDimens)
  }

  function getSetsWithNPlayerTiles (gameState, n) {
    const playerSymbol = gameState.playerSymbol
    return getBoardInSets(gameState).filter((set) => {
      const numPlayerTiles = set.reduce((accum, currentTile) => {
        return currentTile.value === playerSymbol ? accum + 1 : accum
      }, 0)
      return numPlayerTiles === n
    })
  }

  function getBoardInSets (gameState) {
    return [
      columnOfLastPlayed(gameState),
      rowOflastPlayed(gameState),
      primDiagonalOfLastPlayed(gameState),
      secDiagonalOfLastPlayed(gameState)
    ]
  }

  function columnOfLastPlayed ({ gameBoard, lastPlayed, boardDimens }) {
    const colIndex = lastPlayed % boardDimens
    const columnArray = []
    for (let i = colIndex; i < gameBoard.length; i += boardDimens) {
      columnArray.push({ index: i, value: gameBoard[i] })
    }
    return columnArray
  }

  function rowOflastPlayed ({ gameBoard, lastPlayed, boardDimens }) {
    const startIndex = Math.floor(lastPlayed / boardDimens) * boardDimens
    const endIndex = startIndex + boardDimens
    const rowArray = []
    for (let i = startIndex; i < endIndex; i++) {
      rowArray.push({ index: i, value: gameBoard[i] })
    }
    return rowArray
  }

  function primDiagonalOfLastPlayed ({ gameBoard, boardDimens }) {
    const primDiagonalArray = []
    for (let i = 0; i < gameBoard.length; i += boardDimens + 1) {
      primDiagonalArray.push({ index: i, value: gameBoard[i] })
    }
    return primDiagonalArray
  }

  function secDiagonalOfLastPlayed ({ gameBoard, boardDimens }) {
    const secDiagonalArray = []
    for (let i = boardDimens - 1; i < gameBoard.length - 1; i += boardDimens - 1) {
      secDiagonalArray.push({ index: i, value: gameBoard[i] })
    }
    return secDiagonalArray
  }

  function createGameState (gameBoard, lastPlayed, playerSymbol, boardDimens) {
    let newBoard = gameBoard.slice()
    newBoard[lastPlayed] = playerSymbol
    let gameState = {
      gameBoard: newBoard,
      boardDimens,
      playerSymbol,
      lastPlayed
    }
    return gameState
  }

  function calcAnswerMove ({ playerSymbol, gameBoard, boardDimens }, depth) {
    const terminatedScore = rateGameState(arguments[0], depth)
    if (terminatedScore) {
      return terminatedScore
    }
    if (currentGameState.playerSymbol === 'firstMove') {
      return {index: 0}
    }
    const AIMove = isAIMove(playerSymbol)
    const answerSymbol = !AIMove ? currentGameState.answerSymbol : currentGameState.playerSymbol
    let bestMove = {
      score: AIMove ? -10000000 : 10000000,
      index: 0
    }
    for (let tileIndex = 0; tileIndex < gameBoard.length; tileIndex++) {
      if (!gameBoard[tileIndex]) {
        const gameStateToRate = createGameState(gameBoard, tileIndex, answerSymbol, boardDimens)
        const currMove = calcAnswerMove(gameStateToRate, depth + 1)
        const isBestAIMove = currMove.score > bestMove.score && AIMove
        const isBestHumanMove = currMove.score < bestMove.score && !AIMove
        if (isBestAIMove || isBestHumanMove) {
          bestMove.index = tileIndex
          bestMove.score = currMove.score
        }
      }
    }
    return bestMove
  }

  function isAIMove (playerSymbol) {
    return playerSymbol !== currentGameState.playerSymbol
  }

  function rateGameState (gameState, depth) {
    if (hasWon(gameState)) {
      return isAIMove(gameState.playerSymbol) ? {score: depth - 10} : {score: 10 - depth}
    } else if (isDraw(gameState)) {
      return {score: 0}
    }
    return null
  }

  return {
    ttt: ttt
  }
})()



