<template>
<div>
  <h2>it's {{players[activePlayer].name}}'s move </h2>
  <img :src="`http://localhost:3002/avatars/50/${players[0].name}`" alt="">
  <img :src="`http://localhost:3002/avatars/50/${players[1].name}`" alt="">
  <div class="justify-content-center d-flex">
    <div class="d-flex flex-wrap  col-6 game-board">
      <div class="bg"></div>
      <div class="col-4 cell"
      v-for="(cell, index) in gameBoard"
      :key="index" @click="handleCellClick(index)">
        <x-cell v-if="gameBoard[index] === 'x'" :color="players[getIndexByPlayerSymbol('x')].color"></x-cell>
        <o-cell v-else-if="gameBoard[index] === 'o'" :color="players[getIndexByPlayerSymbol('o')].color"></o-cell>
        <svg v-else="" viewBox="0 0 100.000000 100.000000"></svg>
      </div>
    </div>
  </div>
  <div v-if="isWon || isDraw" class="modal-show">
      <h1 v-if="isWon">{{players[activePlayer + 1 % 2].name}} has won</h1>
      <h1 v-else-if="isDraw">It's a draw!</h1>
      <button @click="resetGame()" class="btn btn-danger">reset</button>
  </div>
</div>
</template>

<script>
import Vue from 'vue'
import ttt from '../lib/index'
import OCell from '@/components/OCell.vue'
import XCell from '@/components/XCell.vue'
export default {
  name: 'GameView',
  components: {
    OCell,
    XCell
  },
  data () {
    return {
      isWon: false,
      isDraw: false,
      lastPlayed: -1,
      // eslint-disable-next-line
      gameBoard: [,,,,,,,,,],
      activePlayer: Math.round(Math.random()),
      startPlayer: undefined,
      isEnemysMove: undefined,
      players: [{
        symbol: 'x',
        name: 'M1nd',
        color: '#34f2e3'
      },
      {
        symbol: 'o',
        name: 'Wintermute',
        color: '#23fe23'
      }]
    }
  },
  computed: {

  },
  mounted () {
    this.$nextTick(() => {
      this.startPlayer = this.activePlayer
      this.isEnemysMove = (this.activePlayer !== 0)
      if (this.isEnemysMove) {
        this.assessMove()
      }
    })
    this.players.forEach((pl, index) => {
      fetch(`http://localhost:3002/avatars/list/${this.players[index].name}`)
        .then(res => res.json())
        .then(body => { this.players[index].color = body.face.color })
        .catch(err => console.log(err))
    })
  },
  methods: {
    handleCellClick (index) {
      if (!this.isEnemysMove && !this.gameBoard[index]) {
        Vue.set(this.gameBoard, index, this.getActivePlayersSymbol())
        this.lastPlayed = index
        this.isEnemysMove = true
        this.switchPlayer()
        this.assessMove()
      }
    },
    getActivePlayersSymbol () {
      return this.players[this.activePlayer].symbol
    },
    getIndexByPlayerSymbol (symbol) {
      return this.players.map(e => e.symbol).indexOf(symbol)
    },
    switchPlayer () {
      this.activePlayer = (this.activePlayer + 1) % 2
    },
    assessMove () {
      const assessedMove = ttt.ttt({gameBoard: this.gameBoard, lastPlayed: this.lastPlayed, answerSymbol: this.getActivePlayersSymbol()})
      this.isDraw = assessedMove.isDraw
      this.isWon = assessedMove.hasWon
      if (this.isEnemysMove) {
        setTimeout(() => {
          Vue.set(this.gameBoard, assessedMove.answerMove, this.getActivePlayersSymbol())
          this.lastPlayed = assessedMove.answerMove
          this.isEnemysMove = !this.isEnemysMove
          this.switchPlayer()
          this.assessMove()
        }, 400)
      }
    },
    resetGame () {
      this.startPlayer++
      this.activePlayer = this.startPlayer % 2
      this.gameBoard.map((x, i) => Vue.set(this.gameBoard, i, undefined))
      this.isDraw = false
      this.isWon = false
      this.isEnemysMove = (this.activePlayer !== 0)
      this.lastPlayed = -1
      this.assessMove()
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.game-board{
  padding: 0;
}

#app{
  background: #000;
}

.bg{
  position: absolute;
  width: 94%;
  height: 94%;
  top: 3%;
  left: 3%;
}
.cell{
  padding: 0 0 calc(100% / 3);
}
.cell svg {
  background: #666;
  position: absolute;
  width: 94%;
  top: 3%;
  left: 3%;
  /* margin: 4%; */
}

.modal-show{
  width: 70vw;
  padding: 50px;
  background: rgba(8, 17, 95, 0.8);
  position: fixed;
  left: 15vw;
  top: 15vh;
  z-index: 500;
}

</style>



